@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700;900&display=swap');

body, html {
  width: 100%;
  min-height: 100%;
  font-size: 16px;
  font-family: Roboto;
}

*, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: linear-gradient(rgb(29, 28, 28), rgb(39, 38, 38), rgb(52, 50, 50));
}


.app {
  margin-top: 1.2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.form {
  display: flex;
  gap: .5rem;
}

.form > input {
  position: relative;
  border: none;
  outline: none;
  background-color: transparent;
  font-size: 1.5rem;
  color: white;
  font-weight: 500;
  border-bottom: 2px solid white;
  opacity: .6;
  transition: opacity .25s;
}

.form > input:hover, .form > input:active, .form > input:focus {
  opacity: 1;
}


.form > button {
  background-color: transparent;
  border: 2px solid white;
  color: white;
  font-size: 1.5rem;
  padding: 0 1rem;
  border-radius: 4px;
  cursor: pointer;
  opacity: .75;
  transition: opacity .25s;
  box-shadow: 0 0 4px rgba(255, 255, 255, .7);
}

.form > button:hover, .form > button:active {
  opacity: 1;
}

.todo-wrapper {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-top: 2rem;
  width: 70%;
}

.item {
  width: 100%;
  display: flex;
  justify-content: space-between;
  font-size: 1.5rem;
  color: white;
  font-weight: 500;
  animation: appear 1s;
}

.item button {
  background-color: transparent;
  border: 2px solid white;
  color: white;
  font-size: 1.5rem;
  padding: 0 1rem;
  border-radius: 4px;
  cursor: pointer;
  opacity: .75;
  transition: opacity .25s;
  box-shadow: 0 0 4px rgba(255, 255, 255, .7);
}

.item button:hover, .item button:active {
  opacity: 1;
}

@keyframes appear {
  0% {
    transform: scale(.5);
  }

  100% {
    transform: scale(1);
  }
}